import React, { Component } from 'react';

class PrimaryButton extends Component {
  render() {
    return(
      <div>
        <button onClick={this.props.handleOnClick}>Search</button>

        <style jsx>{`
          button {
            height: 64px;
            width: 183px;
            border-radius: 5px;
            background: rgb(64 102 149);
            color: white;
            font-size: 1.063em;
            font-weight: 600;
            cursor: pointer;
          }
        `}</style>
      </div>
    );
  }
}

ProductThumbnail.propTypes = {
  id: PropTypes.string,
  name: PropTypes.string.isRequired,
  handleOnClick: PropTypes.string.isRequired,
};

export default PrimaryButton;


